body, html{
  width: 100vw;
  margin: 0;
  overflow-x: hidden;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:focus, input{
  outline: none;
}

@font-face{
  font-family: druk;
  src: url(../assets/DrukTextWide-Medium.ttf);
}

@font-face{
  font-family: logo;
  src: url(../assets/Futurist6font.otf);
}

a{
  text-decoration: none;
  display: block;
  font-size: 12px;
  font-weight: 100;
  text-transform: uppercase;
}

button{
  border: none;
  cursor: pointer;
}

h1, p, a, button{
  font-family: druk;
}

h1{
  font-size: 6vw;
  text-transform: uppercase;
}

.cta{
  padding: 20px 40px;
  background: #000;
  color: #fff;
  display: block;
  width: auto;
  margin: auto;
}

.para{
  font-size: 25px;
  opacity: .5;
}


.row{
  display: block;
  overflow: hidden;

  & > span{
    display: block;
    white-space: nowrap;
    transition: .4s cubic-bezier(0.215, 0.61, 0.355, 1)
  }
}






//Menu

nav{
  width: 100%;
  padding: 6vh 6vw 0 6vw;
  display: flex;
  justify-content: space-between;
  position: fixed;
  z-index: 100;
}

.logo{
  font-family: logo;
  font-size: 20px
}

.nav_desktop{
  display: none;
  position: relative;
  width: 100%;

  div{
    display: flex;
    align-items: center;
    position: absolute;
    left: 50%;
    transform: translate(-55%, 0);

    a{
      padding: 0 20px;
      color: inherit;
    }
  }

  & > a{
    position: absolute;
    right: 0;
  }

  .cta{
    transform: translate(0, -36%);
  }
}

#nav_mobile{
  width: 100vw;
  height: 100vh;
  color: #000;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;

  div{
    padding: 20vh 5vw;
  }

  video{
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    transition: .4s;
  }

  &::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    transition: .6s;
  }

  & > div > a > span{
    display: block;
    overflow: hidden;
    transition: .4s;
  }

  & > div > a > span > span{
    display: block;
    transition: .6s;
  }

  a{
    color: #000;
    margin: 22px 0;
    font-size: 24px;

    &:last-child{
      margin-top: 55px;
      font-size: 12px;
    }
  }
}

.nav_mobile-hidden{
  pointer-events: none;

  video{
    opacity: 0;
  }

  &::before{
    transform: translate(0, -100%);
    transition-delay: .8s;
  }

  & > div > a > span > span{
    transform: translate(0, 100%);
  }
}

.nav_mobile-visible{
  pointer-events: all;

  video{
    opacity: 1;
  }

  &::before{
    transform: translate(0, 0);
    transition-delay: 0;
  }

  & > div > a > span > span{
    transform: translate(0, 0);
    transition-delay: .6s;
  }
}


#menu-toggle {
  width: 20px;
  height: 30px;
  position: relative;
  cursor: pointer;

  span{
    display: block;
    background-color: #000;
    transition: .4s;
  }
  #hamburger {
    position:absolute;
    height:100%;
    width:140%;
    transform: translate(-40%, 0);
    span {
      widows: 100%;
      height:2px;
      margin:5px 0;
      &:nth-child(1){
        transition-delay:.6s;
      }
      &:nth-child(2){
        transition-delay:.625s;
        width: 60%;
      }
    }
  }
  #cross {
    position:absolute;
    height:100%;
    width:100%;
    transform:rotate(45deg);
    span{
      &:nth-child(1){
        height:0%;
        width:2px;
        position:absolute;
        top:-7%;
        left:30%;
        transition-delay:0s;
      }
      &:nth-child(2){
        width:0%;
        height:2px;
        position:absolute;
        left:-14%;
        top:24%;
        transition-delay:.25s;
      }
    }
  }
}

#menu-toggle.open {
  #hamburger {
    span {
      width:0%;
      &:nth-child(1){
        transition-delay:0s;
      }
      &:nth-child(2){
        transition-delay:.125s;
      }
    }
  }
  #cross {
    span {
      &:nth-child(1){
        height:70%;
        transition-delay:.625s;
      }
    }
    span {
      &:nth-child(2){
        width:100%;
        transition-delay:.375s;
      }
    }    
  }
}


.cookies{
  position: fixed;
  bottom: 5vh;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 1vw 3.2vh;
  border: 3px solid #000;
  background: #fff;
  display: flex;
  align-items: center;
  z-index: 8;
  font-size: 12px;

  p{
    color: #000;
    white-space: nowrap;
  }

  a{
    background: #000;
    color: #fff;
    padding: 10px 15px;
    margin-left: 20px;
  }
}



// MAIN CONTENT

.main{
  transition: .4s;
  transition-property: background-color;
}

//BANNER

.banner{
  width: 100vw;
  padding: 25vh 0;
  position: relative;
  text-align: center;

  
  h1{
    margin-bottom: 40px;
  }

  @keyframes bnr_btn {
    from{opacity: 0;}
    to{opacity: 1;}
  }

  .cta{
    margin-top: 50px;
    opacity: 1;
    animation-delay: .4s;
    transition: .4s;
    padding: 30px 75px;
    font-size: 10px;
  }

  video{
    position: relative;
    z-index: -1;
    width: 100%;
  }
}


.feat-section{
  padding: 15vh 0;
  width: 100%;
  text-align: center;
  position: relative;
  margin: 5vh 0;
  background: #fff;
  //display: none;
  
  & > div{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    //overflow-x: hidden;
  }

  & > div > div{
    flex: 0 1 50%;
  }

  .feat_video{
    overflow: hidden;
    min-width: 100%;
    max-width: 59%;

    video{
      width: 130%;
    }
  }

  .--video-right{
    video{
      transform: translate(-23%, 0);
    }
  }

  .feat-logo{
    position: absolute;
    width: 32vw;
    z-index: 2;
  }

  .--top{
    top: 2vh;
    right: -6vw;
  }

  .--bottom{
    bottom: 24vh;
    left: -6vw;
  }
}


.payment{
  text-align: center;
  margin-top: 20vh;

  .text-anim{
    &:first-child > span{
      font-size: 18vw;
    }
  }

  video{
    transform: translate(-12%, -20%);
    width: 120%;
    position: relative;
    z-index: -1;
  }
}

#js-btw{
  color: #000;
  transition: .6s;
}


.sliders{
  text-align: center;
  //overflow-x: hidden;
  padding-bottom: 40vh;
  //position: relative;

  .text-anim{
    margin-bottom: 10vh;
    color: #fff;
  }   
}

.notify{
  margin-bottom: 10vh;
  position: relative;

  a{
    text-align: center;
    border: 1px solid #fff;
    border-radius: 100px;
    padding: 25px 55px;
    margin: auto;
    font-size: 10px;
    width: max-content;
    color: #fff;
  }

  form{
    display: block;
    align-items: flex-end;
    margin: auto;
    width: 90%;
    transition: .4s;

    .field{
      position: relative;
      width: 100%;
      margin-bottom: 40px;

      .line{
        position: absolute;
        bottom: 0;
        display: block;
        height: 2px;
        background: #fff;
        opacity: .5;
        transition: .5s ease;
        border-radius: 2px;
      }
  
      & input:focus + .line{
        opacity: 1;
      }
    }
  }

  input{
    display: block;
    border: none;
    font-family: druk;
    font-weight: 100;
    font-size: 13px;
    color: #fff;
    padding: 30px 0;
    background: none;
    overflow: hidden;
  }

  button{
    overflow: hidden;
    transition: .4s;
    //margin-left: 10px;
    background: #fff;
  }

  .f-hidden{
    opacity: 0;

    .line{
      width: 0;
      left: 50%;
    }

    input{
      width: 0;
      padding: 30px 0;
    }

    button{
      width: 0;
      padding: 50px 0;
    }
  }


  .f-visible{
    opacity: 1;

    .line{
      width: 100%;
      left: 0;
      background: rgb(190, 190, 190);
    }

    button{
      width: 100%;
      padding: 40px 30px;
    }

    input{
      width: 100%;
      padding: 30px 0;
    }
  }
}

.pro-slider:hover img{
  opacity: .5;
}

.pro-slider:hover img:hover{
  opacity: 1;
}

.pro-slider img{
  transition: .4s;
}

.slider-logos{
  width: 100%;

  &:hover img{
    opacity: .5;
  }

  &:hover img:hover{
    opacity: 1;
  }

  div{
    width: 100%;
    display: flex;
    align-items: center;

    img{
      width: 33.33%;
      padding: 8vw 8vw;
      transition: .4s;
    }
  }
}


.pro-slider{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 15vh;
  place-items: center;

  & > div{
    width: 60%;

    img{
      width: 100%;
    }
  }
}
  
.c-pro1 { grid-area: 1 / 1 / 2 / 2; }
.c-pro2 { grid-area: 2 / 2 / 3 / 3; img{width: 70% !important;}}
.c-pro3 { grid-area: 1 / 3 / 2 / 4; }
.c-pro4 { grid-area: 3 / 3 / 4 / 4; }
.c-pro5 { grid-area: 3 / 1 / 4 / 2; img{width: 78% !important;}}

.pla{
  margin-top: 30vh;
}


.pre{
  text-align: center;

  .pre-text{
    display: block;
    padding-bottom: 15%;

    & > div:last-child{
      width: 64%;
      margin: -12% auto 0 auto;
    }
  }

  .cta{
    background-color: #fff;
    color: #000;
    padding: 40px 80px;
  }
}


.footer{
  overflow: hidden;

  .footer-heading{
    position: relative;
    z-index: 2;

    h1{
      font-size: 8vw;
    }
  }

  .footer-video{
    margin-top: -30%;
    width: 100%;
    overflow-x: hidden;
    //position: relative;
    //z-index: 1;

    video{
      width: 180%;
      transform: translate(-20%, 0);
    }
  }

  .footer-form{
    margin-top: -20vh;
    position: relative;
    z-index: 3;

    form{
      display: flex;
      width: 90%;
      margin: auto;

      input{
        width: 55%;
        font-family: druk;
        background-color: #F5F5F5;
        color: #444;
      }

      button{
        width: 45%;
        background-color: #000;
        color: #fff;
      }

      input, button{
        padding: 45px 20px;
        border: none;
        font-size: 12px;
      }
    }
  }


  p{
    font-size: 10px;
    text-align: center;
    padding-top: 50px;
    padding-bottom: 40px;
    text-transform: uppercase;
  }

}


h1 > .row:first-child{
  margin-bottom: 15px;
}

.scrub{
  white-space: nowrap;
  display: inline-block;
}

.scrub > span{
  display: inline-block;
}

